import Footer from "./components/Footer";
import "./App.css";
import Response from "./components/response";
import Nav from "./components/Nav";
import NavMobile from "./components/NavMobile";

function App() {
  const check = () => {
    let street = document.querySelector("#street").value;
    let mark = document.querySelector("#mark").value;

    let correct = 0;
    if (streets.indexOf(street) >= 0) {
      correct += 1;
    }
    if (marks.indexOf(mark) >= 0) {
      correct += 1;
    }

    answer(correct);
  };
  const answer = (correct) => {
    document.querySelector(".form").style.display = "none";
    if (correct === 0) {
      document.querySelector(".an0").style.display = "block";
    } else if (correct === 1) {
      document.querySelector(".an1").style.display = "block";
    } else if (correct === 2) {
      document.querySelector(".an2").style.display = "block";
    }
  };

  const marks = [
    "المطار",
    "مطار مارين",
    "مطار مارين الدولي",
    "مطار الدولي",
    "المطار الدولي",
    "المطار مارين",
  ];
  const streets = ["حي الناجين", "الناجين", "الناجي"];

  return (
    <main>
      <Nav />
      <NavMobile />
      <div className="header">
        <h1>العرض الاخير</h1>
      </div>
      <div className="welcome">
        <h2 style={{ color: "#c22735" }}>مرحباً أيها المحقق</h2>
        <p>هل استطعت الوصول الى مكان الطفلة؟</p>
        <p>اذا توصلت الى مكان احتجاز الطفلة قم بإدخال المعلومات في الاسفل</p>
      </div>

      <div className="main_container">
        <Response />

        <div className="form">
          <p>
            محققنا كي نستطيع انقاذ نايا نحتاج منك ان تقوم بكتابة مكان احتجاز
            الطفلة
          </p>

          <div className="app_container">
            <div>
              <label htmlFor="street">اسم الحي</label>
              <br />
              <input
                type="text"
                id="street"
                name="street"
                className="text_input"
              />
            </div>
            <div>
              <label htmlFor="mark">اقرب معلم</label>
              <br />
              <input type="text" id="mark" name="mark" className="text_input" />
            </div>
          </div>
          <div>
            <input
              className="btns_app"
              type="button"
              value="تحقق"
              onClick={() => {
                check();
              }}
            />
            <input
              className="btns_app hint_btn"
              type="button"
              value="تلميح"
              onClick={() => {
                document.querySelector(".answer").style.display = "none";
                document.querySelector(".form").style.display = "none";
                document.querySelector(".an0").style.display = "none";
                document.querySelector(".hint1").style.display = "block";
              }}
            />
          </div>
        </div>
      </div>
      <Footer />
    </main>
  );
}

export default App;
